import React from "react";
import Chicoscience from "../../assets/chicoscience.png";
import Artesanato from "../../assets/artesanato.png";
import Frevo from "../../assets/frevo.png";
import Frutas from "../../assets/frutas.png";
import "./style.css";

export default function Midle() {
    return (
        <div >
            <div class="midle">
                <div class="img-master">
                    <div className="img">
                        <img src={Frevo} alt="frevo" width={300} height={300} />
                    </div>

                    <div className="img">
                        <img src={Chicoscience} alt="chicoscience" width={300} height={300} />
                    </div>

                    <div className="img">
                        <img src={Frutas} alt="frutas" width={300} height={300} />
                    </div>

                    <div className="img">
                        <img src={Artesanato} alt="artesanato" width={300} height={300} />
                    </div>
                </div>
             <div className="text-master">
                <h1>O ambiente dos artistas</h1>
                <h2>O Habitarte surgiu ao enxergar a grande dificuldade enfrentada pelos artistas em tentar ganhar visibilidade no mercado cultural. At mesmo antes da pandemia esse problema sempre foi uma difcil realidade para muitos que tentam seguir uma carreira artstica e no sabem como expor seu trabalho. Com o habitarte os artistas podero criar um portflio com contedos artsticos (Vdeos, fotos e udios). que sero expostos para possveis contratantes, empresrios, produtores culturais e assim como qualquer pessoa que se interesse por contedos culturais. </h2>
            </div>   
                
                
            </div>
        </div>


    );
}